.card-ubi {
  display: grid;
  grid-template-columns: 300px;
  grid-template-rows: 210px 210px 80px;
  grid-template-areas: "image" "text" "stats";
  border-radius: 18px;
  background: white;
  box-shadow: 5px 5px 15px rgba(0,0,0,0.9);
  text-align: center;
  transition: 0 .5s ease;
  margin: 30px;
}

.card-image-ubi {
  grid-area: "image";
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  background-size: cover;
  width: 100%;
}

.card-text-ubi {
grid-area: text;
  margin: 20px;
}

.card-text-ubi p {
  color: grey;
  font-size:15px;
  font-weight: 300;
}

.card-text-ubi h3 {
  margin-top:0px;
  font-size:28px;
}

.card-stats-ubi {
  grid-area: "stats"; 
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  background: rgb(255, 7, 110);
}

.card-stats-ubi .stat-ubi {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: white;
  padding:10px;
}

.card-ubi:hover {
  transform: scale( 1.10 ) ;
  box-shadow: 5px 5px 15px rgba( 0 , 0 , 0 , 0.6 ) ;
}
          
.fav-ubi {
  display: flex;
  text-align: center;
  height: 16px;
  width: 20px;
  justify-content: center;
  align-items: center;
  border: 1px solid #0000005e;
  padding: 5px;
  margin: 3px;
  border-radius: 5px;
  transition: background-color 0.2s ease;
}

.fav-ubi:hover {
  cursor: pointer;
  background-color: rgb(112, 8, 52);
}

.expand-ubi{
  color: white;
}